/* jshint ignore:start */

define('valoritization/config/environment', ['ember'], function(Ember) {
  var prefix = 'valoritization';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("valoritization/tests/test-helper");
} else {
  require("valoritization/app")["default"].create({"host":"http://localhost:3000","name":"valoritization","version":"0.0.0+756161d1"});
}

/* jshint ignore:end */
